<template>
  <div class="container">

    <h1>Github User Search</h1>
    <p>Made with VueJS and RxJS</p>

    <header>

      <div class="searchbox">
        <input 
          type="text" 
          placeholder="Search for Github user, e.g. octocat"
          @input="getUsers($event.target.value)" 
          @keyup.esc="showUsersList = false" 
          @blur="removeFocus($event)" 
          @keydown="listControl($event)" 
          @focus="reset"
          v-model="query">
      </div>

      <div class="results">
        <ul ref="list" v-if="showUsersList">
          <li v-for="(user, i) in show20Users" 
            :key="user.login" 
            :class="{'hilite': i === currentPos}" 
            @mouseenter="currentPos = i" 
            @mousedown="getUserInfo(user.login)">
            {{ user.login }}
          </li>
        </ul>
      </div>

    </header>
    
    <section>

      <div class="info" v-if="showUserInfo">
        <h1>{{ info.login }}</h1>
        <p>{{ info.location }}</p>
        <img ref="avatar" :src="info.avatar_url">
        <ul>
          <li>Gists <span>{{ info.public_gists }}</span></li>
          <li>Repos <span>{{ info.public_repos }}</span></li>
        </ul>
      </div>

    </section>
    
  </div>
</template>

<script>

  import { Observable } from 'rxjs/Observable';
  import 'rxjs/add/observable/from';
  import 'rxjs/add/operator/filter';
  import 'rxjs/add/operator/debounceTime';
  import 'rxjs/add/operator/distinctUntilChanged';
  import axios from 'axios';

  export default {
    
    data() {
      return {
        query: '',
        users: [],
        info: {},
        currentPos: 0,
        showUsersList: false,
        showUserInfo: false
      }
    },

    computed: {
      show20Users() {
        return this.users.splice(0, 20);
      }
    },

    methods: {
      
      getUsers(search) {
        let obs = Observable.create((observer) => {
          observer.next(search);
        });
        obs
          .filter(text => text.length > 2)
          .debounceTime(500)
          .distinctUntilChanged()
          .subscribe(query => {
            return axios.get('https://api.github.com/search/users?q=' + query + '+in:login')
              .then(res => {
                this.users = res.data.items;
                this.showUsersList = true;
              });
          });
      },

      getUserInfo(user) {
        return axios.get('https://api.github.com/users/' + user)
          .then(res => {
            this.info = res.data;
            this.showUsersList = false;
            this.showUserInfo = true;
          });
      },

      reset() {
        this.query = '';
        this.showUsersList = false;
        this.showUserInfo = false;
        this.currentPos = 0;
      },

      removeFocus(event) {
        if(this.showUserInfo) {
          event.target.$el.blur();
        }
      },

      listControl(event) {
          switch(event.keyCode) {
            case 38:
              if(this.currentPos < 1) {
                this.currentPos = 0;
              } else {
                this.currentPos -= 1;
              }
            break;
            case 40:
              if(this.currentPos === (this.show20Users.length - 1)) {
                this.currentPos = (this.show20Users.length - 1);
              } else {
                this.currentPos += 1;
              }
            break;
            case 13:
              this.getUserInfo(this.show20Users[this.currentPos].login);
              return;
            break;
          }
        }
    }
  }

  
</script>

<style scoped>
  @import url('/static/css/styles.css');

  .normal {
    background-color: rgba(242,242,242,0.5);
  }
  .hilite {
    background-color:rgba(65,184,131,0.5);
    cursor: pointer;
  }
  #currentPos {
    visibility: hidden;
  }
</style>


